/* ==========================================================================
  Mixins
  ========================================================================== */

/*
  Clearfix
  ========================================================================== */

@mixin clearfix {
    &::after {
        content: "";
        display: block;
        clear: both;
    }
}

/*
  Object fit polyfill
  ========================================================================== */

@mixin object-fit($attr) {
    object-fit: $attr;
    font-family: "object-fit: #{$attr}";
}

/*
  Object fit image
  ========================================================================== */

@mixin object-fit-image {
    @include object-fit(cover);
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: block;
}

/*
  Header Styling
  ========================================================================== */

@mixin header-style {
    margin-top: 0;
    margin-bottom: var(--spacing-md);

    font-family: var(--font-family-alpha);
    font-weight: var(--font-weight-bold);
    color: var(--colour-alpha);
}

/*
  Consistent spacing
  ========================================================================== */

@mixin mobile-gutter {
    padding-left: var(--gutter-sm);
    padding-right: var(--gutter-sm);

    @media ($mq-xxs-min) {
        padding-left: var(--gutter-md);
        padding-right: var(--gutter-md);
    }

    @media ($mq-xl-min) {
        padding-left: var(--gutter-lg);
        padding-right: var(--gutter-lg);
    }
}

/*
  Input
  ========================================================================== */

@mixin input {
    font-size: inherit;
    color: var(--colour--black);
    background-color: var(--colour-white);
    background-repeat: no-repeat;
    border: 1px solid var(--colour--grey-light);
    border-radius: 0;

    &:focus {
        outline: 0;
    }

    &:disabled {
        opacity: .6;
    }
}
